import React from "react";
import classNames from "classnames/bind";
import styles from "./s.module.scss";
const cx = classNames.bind(styles);

export default function PostThum() {
  return (
    <>
      <div className={cx("PostThum")}>
        <div className={cx("Header")}>
          <div className={cx("Avatar")}></div>
          <div className={cx("TitleL")}>B0005 - Nha Pho</div>
          <div className={cx("TitleS")}>• 22/01/2022</div>
          <div className={cx("Date")}>• Quan Binh Thanh, Tp.HCM</div>
          <div className={cx("Favorite")}>
            <span className="material-icons">favorite_border</span>
          </div>
        </div>
        <div className={cx("Body")}>
          <div className={cx("Photo")}></div>
        </div>
        <div className={cx("Bottom")}>
          <div className={cx("Content")}>
            Diện tích 3m nở hậu 8m x25m. Được phép xây dựng 1 hầm, 1 trệt, 1
            lửng, 3 lầu. Ngân hàng hổ trợ vay 1.7 tỷ.
          </div>
          <div className={cx("Price")}>99,75 ty VND</div>
          <div className={cx("Chitiet")}>Chi tiet</div>
          <div className={cx("CopyRight")}>
            <p>
              <b>bds.vngate.top</b> - tim voi ID: <b>B0005</b>
            </p>
          </div>
        </div>
      </div>
    </>
  );
}
